section::after {
    content: "";
    display: table;
    clear: both;
}

footer {
    
    padding: 10px;
    text-align: center;
    width: 1262px;
    height: 300px;
    position: -webkit-sticky;
    position: relative;
    bottom: 1px;
    border-top: 1px solid rgba(126, 121, 121, 0.2);

}

footer div.premier {
    display: inline-block;
    padding: 10px 10px;
    margin: 5px 16px;
    text-align: left;
}

footer h5 {
    font-size: 32px;
    line-height: 2;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-weight: 500;
    color: #CEAD6D;
}

footer a {
    font-size: 17px;
    margin-left: 10px;
    color: white;
    font-weight: 500;
    text-decoration: none;
    background-color: transparent;
}

footer a:hover {

    color: #CEAD6D;

    text-decoration: none;
}

input[type=text],
input[type=email] {
    box-sizing: border-box;
    border: 2px solid #CEAD6D;
    border-radius: 4px;
    font-size: 13px;
    background-color: white;
    padding: 10px 12px 10px 12px;
}

input[type=text]:hover,
input[type=email]:hover {
    background-color: #E1DCE0;

}

input[type=text]:focus,
input[type=email]:focus {
    border: 3px solid #CEAD6D;
    background-color: white;
}

input[type=submit] {
    background-color: #CEAD6D;
    border-radius: 4px;
    border: 1px solid #CEAD6D;
    color: white;
    padding: 11px 12px;
    text-decoration: none;
    margin: 4px 2px;
    cursor: pointer;
}

input[type=submit]:hover {
    background-color: white;
    color: black;
}

input[type=submit]:focus {
    background-color: white;
    color: black;
}

footer h3 {
    display: inline-block;
    margin-right: 30px;
    padding-right: 5px;
    color: #CEAD6D;
    font-size: 30px;
}

footer h1 {
    border: 2px solid #CEAD6D;
    display: inline-block;
    margin-right: 30px;
    padding-right: 5px;
    color: #CEAD6D;
    font-size: 30px;
}

footer button {
    border: 2px solid #CEAD6D;
    background-color: transparent;
    margin-right: 30px;
    float: right;
    padding: 5px 10px;
    color: white;
    font-size: 30px;
    cursor: pointer;
}

footer button:hover {
    background-color: #CEAD6D;
}

footer img {
    float: right;

    cursor: pointer;

}

